.posterImage {
    height: 600px;
}

.posterImage>img {
    margin: auto;
    display: block;
    width: 100%;
    height: 100vh;
}

.posterImage__overlay {
    position: absolute;
    padding: 5rem;
    bottom: 0px;
    height: 70%;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgb(0, 0, 0, 0), rgb(0, 0, 0, 1));
    opacity: 1;
    transition: opacity .3s;
}

.posterImage__overlay:hover {
    opacity: 1;
}

.posterImage__title {
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 0.4rem;
    text-align: left;
}

.posterImage__runtime {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.posterImage__rating {
    margin-left: 3rem;
}

.posterImage__description {
    font-style: italic;
    font-size: 1rem;
    margin-bottom: 0.25rem;
    display: flex;
    text-align: left;
    width: 50%;
}

@media screen and (max-width: 650px) {
    .posterImage__overlay {
        display: none;
    }

    .posterImage {
        height: 300px;
    }

    .posterImage>img {
        height: 50vh;
    }
}

@media screen and (max-width: 500px) {
    .posterImage {
        height: 200px;
    }

    .posterImage>img {
        height: 30vh;
    }
}